import React, {Fragment} from "react";
import {Helmet} from "react-helmet";
import {Link} from "react-router-dom";

import questionMark from "../assets/imgs/left-dotted-arrowhead.png";
import selectSound from "../assets/audio/button_click.mp3"

const Home = () => {

const handleEvent = (event => 
    document.getElementById("select-sound").play()
);
       
    return ( 
        <Fragment>
            <Fragment><audio id="select-sound" src={selectSound}></audio></Fragment>
        <Helmet><title>Quiz App - Home</title></Helmet>
        <div id="home" class="page">
            <div className = "page-container">  
                <div className="title" >
                    <h1>WHO AM I QUIZ </h1>
                </div>
              {/* <div>
                <img src={question} alt="question" height="100px" /> 
              </div>*/}
                <div className="play-button-container">
                    <ul>
                        <li id="item-1"><img src={questionMark} alt="questionMark" height="30px" />  <Link className ="nav-link" to="/play/questions" onMouseDown={handleEvent} >PLAY_</Link></li>
                        <li id="item-2"><img src={questionMark} alt="questionMark" height="30px" />  <Link className ="nav-link" to="/play/instructions" onMouseDown={handleEvent}>INSTRUCTIONS_</Link></li>
                    </ul> 
                </div>
            </div>
        </div>
        </Fragment>
    );
};



export default Home;